/* fallback */

@font-face {
  font-family: 'Material Icons Rounded';
  font-style: normal;
  font-weight: 400;
  src: url(./icons-rounded.otf) format('truetype');
  font-display: block;
}

.material-icons {
  font-family: 'Material Icons Rounded';
  font-weight: normal;
  font-style: normal;
  font-size: 1.3em;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  white-space: nowrap;
  word-wrap: normal;
  direction: ltr;
  font-feature-settings: 'liga';
  -webkit-font-smoothing: antialiased;
  overflow: hidden;
  width: 1em;
  box-sizing: content-box;
  color: currentColor;
}
